import React, {useEffect, useMemo, useState} from 'react';
import qs from 'query-string';
import {useWeb3React} from "@web3-react/core";
import {ethers} from "ethers";
import {Box, Button, Flex, Input, Text} from "../../components/uikit";
import Select from "../../components/Select/Select";
import contracts from "../../config/constants/contracts";
import useToast from "../../hooks/useToast";
import {SupportedChainId} from "../../config/constants/chains";


const abiUniV2 = [
  "function approveTokenSpent(address trader, address token, uint256 amount)",
  "function swapExactTokensForTokens(uint amountIn, uint amountOutMin, address[] calldata path, uint deadline)",
  "function tokenAllowance(address trader, address token)"
]
function amountFormat(amount) {
  return Math.floor((+amount || 0) * 10000) / 10000;
}
function Trading(props) {
  const {location} = props;
  const {chainId, library, account, active} = useWeb3React()
  const [search] = useState(qs.parse(location.search))
  const [tokenBalance, setTokenBalance] = useState([])
  const [allowanceAmount, setAllowanceAmount] = useState(0)
  const tokens = useMemo(() => [
    {
      label: 'ST1',
      value: '0x47735caEfa2CCbC2f1C6a37337983dd0442F9755',
    },
    {
      label: 'ST2',
      value: '0xd42D24648b4f4894a8b546fef96c37EBae8cAadB',
    },
  ], [])
  const [swapInfo, setSwapInfo] = useState<{
    input: { amount: any, address: string }, output: { amount: any, address: string }
  }>({
    input: {
      amount: '',
      address: search.token === tokens[0].value ? tokens[0].value: tokens[1].value
    },
    output: {
      amount: '',
      address: search.token === tokens[0].value ? tokens[1].value: tokens[0].value
    }
  })
  const {toastSuccess, toastError} = useToast()
  const onTrade = async () => {
    const signer = library.getSigner(search.trader)
    const iface = new ethers.utils.Interface(abiUniV2)
    const dataSwap = iface.encodeFunctionData("swapExactTokensForTokens", [ethers.utils.parseEther(swapInfo.input.amount as (number | string) as string), "0", [swapInfo.input.address, swapInfo.output.address], "999999999999999"])
    const avatar = new ethers.Contract(contracts.avatar.network[chainId], new ethers.utils.Interface(contracts.avatar.abi))
    try {
      const tx = await avatar.connect(signer.connectUnchecked()).delegateCall(contracts.plugin.network[chainId], dataSwap)
      if (tx) {
        const res = await tx.wait();
        if (res) {
          toastSuccess('Success');
          location.reload()
        }
      }
    } catch (e) {
      toastError('Error', <Flex>
        <Text style={{wordBreak: 'break-word'}}>{e.message}</Text>
      </Flex>)
    }

  }
  const getAmountOut = async (amount) => {
    // eslint-disable-next-line no-restricted-globals
    try {
      if (amount > 0) {
        const swap = new ethers.Contract(contracts.swapV2.network[chainId], new ethers.utils.Interface(contracts.swapV2.abi), library.getSigner(account));
        const [amountIn, amountOut] = await swap.getAmountsOut(ethers.utils.parseEther(String(amount)), [tokens[0].value, tokens[1].value]);
        console.log('amountOut`amountOut: >>>>>', amountOut)
        setSwapInfo(prevState => ({
          output: {
            ...prevState.output,
            amount: amountFormat(ethers.utils.formatEther(amountOut))
          },
          input: prevState.input
        }))
      } else {
        setSwapInfo(prevState => ({
          output: {
            ...prevState.output,
            amount: ''
          },
          input: prevState.input
        }))
      }
    } catch (e) {
      console.log('e: >>>>>', e)
    }
  }


  useEffect(() => {
    (async () => {
      if (chainId === SupportedChainId.GOERLI) {
        const res = []
        // eslint-disable-next-line no-restricted-syntax
        for (const token of tokens) {
          const _token = {
            ...token,
            balance: ''
          }
          const erc20 = new ethers.Contract(token.value, new ethers.utils.Interface(contracts.erc20.abi),  library.getSigner(account));
          // eslint-disable-next-line no-await-in-loop
          const balance = await erc20.balanceOf(contracts.avatar.network[chainId])
          console.log('balance: >>>>>', balance)
          _token.balance = ethers.utils.formatEther(balance)
          res.push(_token)
        }
        setTokenBalance(res)
      }
    })()
  }, [tokens, chainId, account, library])

  useEffect(()=>{
    (async ()=> {
      console.log('active: >>>>>', active)
      try {
        if(active) {
          const plugin = new ethers.Contract(
            contracts.plugin.network[SupportedChainId.GOERLI],
            new ethers.utils.Interface(contracts.plugin.abi),
            library.getSigner(account));
          const allowance = await plugin.tokenAllowance(account, search.token);
          console.log('allowance: >>>>>', allowance)
          // setAllowanceAmount(amountFormat(ethers.utils.formatEther(allowance)))
        }
      } catch (e) {
        console.log('e: >>>>>', e)
      }
    })()
  },[account, library, search, active])
  return (
    <div>
      <Box width={500} marginX='auto'>
        <Flex flexDirection="column">
          <Flex justifyContent="space-between"><Text>Available</Text>
            <Text>{allowanceAmount} {search.symbol}</Text></Flex>
          <Flex>
            <Select onChange={(option) => {
              setSwapInfo(prevState => ({
                ...prevState, input: {
                  amount: prevState.input.amount,
                  address: option.value,
                }
              }))
            }} options={[tokens[0]]}/>
            <Input style={{marginLeft: 10}} type='number' onChange={async (event) => {
              setSwapInfo(prevState => ({
                output: prevState.output,
                input: {
                  ...prevState.input,
                  amount: event.target.value,
                }
              }))
              await getAmountOut(event.target.value)
            }} value={swapInfo.input.amount}/>
          </Flex>
        </Flex>
        <Flex flexDirection="column">
          <Text>Traded</Text>
          <Flex>
            <Select onChange={(option) => {
              setSwapInfo(prevState => ({
                ...prevState, output: {
                  amount: prevState.output.amount,
                  address: option.value,
                }
              }))
            }} options={[tokens[1]]}/>
            <Input style={{marginLeft: 10}} type='number' onChange={(event) => {
              setSwapInfo(prevState => ({
                output: {
                  ...prevState.output,
                  amount: event.target.value
                },
                input: prevState.input
              }))

            }} value={swapInfo.output.amount}/>
          </Flex>
        </Flex>
        {
          tokenBalance.length &&
            <Flex style={{width: "100%", padding: 20}} flexDirection="column">
                <Flex justifyContent="space-around" flexShrink={0} style={{width: '100%'}}>
                    <Box style={{width: '30%'}}>{true}</Box>
                    <Box style={{width: '30%'}}>From</Box>
                    <Box style={{width: '30%'}}>To</Box></Flex>
                <Flex justifyContent="space-around" flexShrink={0} style={{width: '100%'}}>
                    <Box style={{width: '30%'}}>ST1</Box>
                    <Box style={{width: '30%'}}>{amountFormat(tokenBalance[0].balance)}</Box>
                    <Box style={{width: '30%'}}>{amountFormat(+tokenBalance[0].balance - swapInfo.input.amount)}</Box>
                </Flex>
                <Flex justifyContent="space-around" flexShrink={0} style={{width: '100%'}}>
                    <Box style={{width: '30%'}}>ST2</Box>
                    <Box style={{width: '30%'}}>{amountFormat(tokenBalance[1].balance)}</Box>
                    <Box style={{width: '30%'}}>{amountFormat(+tokenBalance[1].balance + swapInfo.output.amount)}</Box>
                </Flex>
            </Flex>
        }
        <Flex flexDirection="column" marginY={20}>
          <Flex flexDirection="column">
            <Button onClick={async () => {
              // await onTokenApprove()
              await onTrade()
            }}>Trade</Button>
          </Flex>
        </Flex>
      </Box>
    </div>
  )
    ;
}

export default Trading;
