import React, {useCallback, useMemo, useState} from 'react';
import styled from "styled-components";
import qs from 'query-string';
import {Contract} from "@ethersproject/contracts";
import {useWeb3React} from "@web3-react/core";
import {ethers} from "ethers";
import {Box, Button, Flex, Input, Text} from "../../components/uikit";
import Select from "../../components/Select/Select";
import contracts from "../../config/constants/contracts";
import useToast from "../../hooks/useToast";
import {SupportedChainId} from "../../config/constants/chains";

const Row = styled(Box)`
  &:hover {
    background-color: #ddd;
    cursor: pointer;
  }
`

function SetTrader(props) {
  const {location} = props;
  const {chainId, active, library, account} = useWeb3React()
  const [search] = useState(qs.parse(location.search))
  const tokens = useMemo(() => [
    {
      label: 'ST1',
      value: '0x47735caEfa2CCbC2f1C6a37337983dd0442F9755'
    },
    {
      label: 'ST2',
      value: '0xd42D24648b4f4894a8b546fef96c37EBae8cAadB'
    },
  ], [])
  const [trader, setTrader] = useState('')
  const [allowanceAmount, setAllowanceAmount] = useState('')
  const [token, setToken] = useState(tokens[0].value)
  const {toastSuccess, toastError} = useToast()
  const [setup, setSetup] = useState(1)
  const [actionLock, setActionLock] = useState(false)
  // const []
  const onApprove = async () => {
    if(active) {
      if(chainId === SupportedChainId.GOERLI) {
        const avatar = new ethers.Contract(contracts.avatar.network[chainId], contracts.avatar.abi, library.getSigner(account).connectUnchecked())

        try {
          setActionLock(true)
          const tx = await avatar.approve(trader, contracts.plugin.network[chainId], true)
          if (tx) {
            const res = await tx.wait();
            if (res) {
              toastSuccess('Success');
              setSetup(2)
              setActionLock(false)
            }
          }
        } catch (e) {
          toastError('Error', <Flex>
            <Text style={{wordBreak: 'break-word'}}>{e.message}</Text>
          </Flex>)
          setActionLock(false)
        }
      }else {
        toastError('Error', <Flex>
          <Text style={{wordBreak: 'break-word'}}>Unsupported network, please switch to goerli</Text>
        </Flex>)
        setActionLock(false)
      }
    }
  }
  const onTransfer = async ()=>{
    const abiUniV2 = [
      "function approveTokenSpent(address trader, address token, uint256 amount)",
      "function swapExactTokensForTokens(uint amountIn, uint amountOutMin, address[] calldata path, uint deadline)",
    ]
    const iface = new ethers.utils.Interface(abiUniV2);
    const dataApprove = iface.encodeFunctionData("approveTokenSpent", [trader, token, ethers.utils.parseEther(allowanceAmount).toString()])
    const signer = library.getSigner(account)
    console.log(': >>>>>', trader, token, ethers.utils.parseEther(allowanceAmount).toString())
    // let data_swap = iface.encodeFunctionData("swapExactTokensForTokens", ["50000000000000000000", "0", [token1.target, token2.target], "999999999999999"])
    const avatar = new ethers.Contract(contracts.avatar.network[chainId], new ethers.utils.Interface(contracts.avatar.abi), signer)
    console.log('avatar: >>>>>', avatar)
    // try to call
    try {
      setActionLock(true)
      const tx= await avatar.delegateCall(contracts.plugin.network[chainId], dataApprove)
      if (tx) {
        const res = await tx.wait();
        if (res) {
          toastSuccess('Success');
          setActionLock(false)
        }
      }
    } catch (e) {
      toastError('Error', <Flex>
        <Text style={{wordBreak: 'break-word'}}>{e.message}</Text>
      </Flex>)
      setActionLock(false)
    }

  }
  return (
    <div>
      <Box width={500} marginX='auto'>
        <h2 style={{marginBottom: 20, fontSize: 20}}>Trader</h2>
        <Flex flexDirection="column">
          <Text>Trader</Text>
          <Input type="text" placeholder="Please enter trader address" onChange={(event) => {
            setTrader(event.target.value)
          }} value={trader}/>
        </Flex>
        <Flex flexDirection="column">
          <Text>Allocation</Text>
          {/* <Dropdown target={<Input value={selectToken} />} position="bottom" >
            {
              tokens.map(it=> <Row key={it.address} onClick={()=>{
                onSelect(it.symbol)
              }}>{it.symbol}</Row>)
            }
          </Dropdown> */}
          <Flex>
            <Select onChange={(option) => {
              setToken(option.value)
            }} options={tokens}/>
            <Input disabled={setup === 1} style={{marginLeft: 10}} type='number' onChange={(event) => {
              setAllowanceAmount(event.target.value)
            }} value={allowanceAmount}/>
          </Flex>
        </Flex>
        <Flex flexDirection="column" marginY={20}>
          <Flex flexDirection="column">
            <Text>setup 1.</Text>
            <Button isLoading={trader === '' || setup === 2 || actionLock} onClick={async () => {
              await onApprove()
            }}>Approve Trader</Button>
          </Flex>
          <Flex flexDirection="column">
            <Text>setup 2.</Text>
            <Button isLoading={(setup === 1 && Number(allowanceAmount) <= 0)|| actionLock} onClick={async () => {
              if(setup === 1) return;
              // await onTokenApprove()
              await onTransfer()

            }}>Allowance</Button>
          </Flex>
        </Flex>
      </Box>
    </div>
  );
}

export default SetTrader;
