import {ethers} from "ethers";
import {useEffect, useState} from "react";
import {useWeb3React} from "@web3-react/core";
import {Contract} from '@ethersproject/contracts'
import contracts from "config/constants/contracts";
import {SupportedChainId} from "../config/constants/chains";
import {INFURA_NETWORK_URLS} from "../config/constants/infura";

interface ApproveTokenSpent {
  trader: string;
  token: string;
  amount: number;
  symbol?: string;
}

const abi = [
  "event Approve(address caller, address callee, bool isApproved)",
  "event UniswapV2Plugin_ApproveTokenSpent(address trader, address token, uint256 amount)"
];
export default function useEvent(address) {
  const {active, library, chainId} = useWeb3React()
  // Replace this with your contract's ABI

// Replace this with your provider
  const [provider] = useState(new ethers.providers.JsonRpcProvider(INFURA_NETWORK_URLS[SupportedChainId.GOERLI]));
  const [contractInterface] = useState(new ethers.utils.Interface(abi));
// Create an event filter
  const event = contractInterface.getEvent('UniswapV2Plugin_ApproveTokenSpent');
  const eventTopic = contractInterface.getEventTopic(event);

// Replace this with your contract's address
//   const contractAddress = '0xYourContractAddress';

  const [logs,setLogs] = useState<ApproveTokenSpent[]>([])
  const [filter] = useState({
    address,
    topics: [ eventTopic ]
  });

  useEffect(()=>{
    if (active) {
      const contract = new Contract(contracts.avatar.network[chainId], abi, provider)
      const _abi = [
        {
          "inputs": [
            {
              "internalType": "address",
              "name": "account",
              "type": "address"
            }
          ],
          "name": "balanceOf",
          "outputs": [
            {
              "internalType": "uint256",
              "name": "",
              "type": "uint256"
            }
          ],
          "stateMutability": "view",
          "type": "function"
        },
        {
          "inputs": [],
          "name": "symbol",
          "outputs": [
            {
              "internalType": "string",
              "name": "",
              "type": "string"
            }
          ],
          "stateMutability": "view",
          "type": "function"
        },
        {
          "inputs": [
            {
              "internalType": "address",
              "name": "owner",
              "type": "address"
            },
            {
              "internalType": "address",
              "name": "spender",
              "type": "address"
            }
          ],
          "name": "allowance",
          "outputs": [
            {
              "internalType": "uint256",
              "name": "",
              "type": "uint256"
            }
          ],
          "stateMutability": "view",
          "type": "function"
        },
      ]
      const erc20inter = new ethers.utils.Interface(_abi)
      contract.queryFilter(filter).then((_logs) => {
        // console.log('_logs: >>>>>', _logs)
        _logs.forEach(async (log) => {
          const decodedLog = contractInterface.parseLog(log).args as unknown as ApproveTokenSpent;

          const erc20Token = new Contract(decodedLog.token, _abi, provider)
          const _symbol = await erc20Token.symbol()
          setLogs(prev => {
            const isHasIndex = prev.findIndex(it=> it.trader === decodedLog.trader && it.token === decodedLog.token);
            if (isHasIndex > -1) {
              const nextState = [...prev];
              nextState[isHasIndex].amount = ethers.utils.formatEther(decodedLog.amount) as string as unknown as number
              return nextState
            }
              return [
                ...prev,
                  {
                    trader: decodedLog.trader,
                    token: decodedLog.token,
                    amount: ethers.utils.formatEther(decodedLog.amount) as string as unknown as number,
                    symbol: _symbol
                  }
              ]

            // return Array.from(new Set([
            //   ...prev,
            //   {
            //     trader: decodedLog.trader,
            //     token: decodedLog.token,
            //     amount: ethers.utils.formatEther(decodedLog.amount) as string as unknown as number,
            //     symbol: _symbol
            //   }
            // ]))
          })
          console.log(decodedLog);
        });
      });
    }
  }, [filter, provider, contractInterface, active, chainId, library])

  return logs;

}
